@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&display=swap');

*,
::after,
::before {
    box-sizing: border-box;
}

:root {
    --color-base: #144A85;
    --color-base1: #1F7CCC;
}

body,
html {
    background: #fff;
    color: #262730;
    padding: 0;
    line-height: 1.4;
    font-size: 14px;
    margin: 0;
    overflow-x: hidden;
    font-family: "Mulish", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

img,
iframe {
    display: block;
    max-width: 100%;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    transition: all 0.4s ease;
    color: inherit;

    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

input {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    border: none;
    outline: none;
    color: #404251;
    padding-left: 25px;
    padding-right: 25px;

    &:focus {
        outline: none;
        box-shadow: none;
    }
}

.form-group {
    margin-bottom: 20px;
}

.form-control {
    border-radius: 24px;
    border: 1px solid #EBEBEB;
    padding-right: 25px;

    &:focus {
        outline: none;
        box-shadow: none;
    }
}

.select2-container--default .select2-selection--single {
    border-radius: 24px;
    height: 40px;
    color: #11233A;
    border: 1px solid #EBEBEB;
    background: #fff;
}

textarea {
    height: auto;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
    line-height: 40px;
    color: #11233A;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    top: 3px;
    right: 8px;

    b {
        border-color: #EBEBEB;
        border-right: 1px solid #EBEBEB;
        border-bottom: 1px solid #EBEBEB;
        width: 7px;
        height: 7px;
        transform: rotate(45deg);
        border-left: 0 solid #000;
        border-top: 0 solid #000;
    }
}

.menu_before {
    width: 100%;
    height: 1px;
}

button,
.btn {
    border: none;
    outline: none;
    cursor: pointer;
    text-align: center;
    padding: 9px 16px;
    font-weight: 500;
    border-radius: 24px;
    display: inline-block;

    &.btn-base {
        background: #1F7CCC;
        color: #FCFCFC;

        &:hover {
            background: #7DBAEC;
        }
    }

    &.btn-line {
        border: 1px solid #1F7CCC;
        background: none;
        color: #145598;
        font-weight: bold;

        &:hover {
            color: #1F7CCC;
        }
    }

    &.btn-white {
        border: 1px solid #fff;
        background: #fff;
        color: var(--color-base1);
    }
}

.container {
    width: 100%;
    max-width: 1230px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;

    @media (max-width:1400px) {
        max-width: 1130px;
    }

}

.select-option {
    position: relative;

    .tt {
