        }
    }

    .list-option {
        display: none;
        position: absolute;
        box-shadow: 0px 4px 30px 0px #3629B712;
        background: #fcfcfc;
        padding: 15px;
        z-index: 1;
        border-radius: 16px;
        top: 50px;
        right: 0;
        box-shadow: 0 0px 8px 0px #3629B714;
        min-width: 300px;
        width: 100%;

        .item {
            white-space: nowrap;
            display: flex;
            margin-bottom: 10px;
            justify-content: space-between;
            align-items: center;

            &:last-child {
                margin-bottom: 0;
            }

            .choose {
                display: flex;
                align-items: center;
                gap: 5px;

                label {
                    display: flex;
                    align-items: center;
                    gap: 5px;

                    img {
                        height: 22px;
                    }
                }
            }


            .price-ship {
                color: #1E222C;
            }
        }
    }
}

.wrap-paging {
    margin: 40px 0;
    margin-left: auto;

    @media (max-width:767px) {
        margin: 20px;
    }

    .pagination {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        justify-content: end;

        @media (max-width:767px) {
            display: none;
        }

        .page {
            font-size: 16px;
            font-weight: 500;
            display: inline-block;
            line-height: 40px;
            text-align: center;
            width: 40px;
            height: 40px;
            background: #FCFCFC;
            box-shadow: 0px 4px 30px 0px #3629B712;
            box-shadow: 4px 0px 8px 0px #3629B714;
            border-radius: 50%;

            a {
                color: #7C8087;
                display: block;
            }

            .icon {
                width: 40px;
                height: 40px;
                padding: 8px;
            }

            &.active {
                background: var(--color-base);

                a {
                    color: #fff
                }

            }

            &.disabled {
                opacity: 0.6;
            }


        }
    }

    .xemthem {
        width: 100%;
        background: var(--color-base);
        color: #FCFCFC;
        text-align: center;
        height: 40px;
        border-radius: 24px;
        line-height: 40px;
        display: none;

        @media (max-width:767px) {
            display: block;
        }
    }
}

.text-base {
    color: var(--color-base);
}

.thumb {
    display: block;
    overflow: hidden;
    height: 0;
    position: relative;
    width: 100%;

    &.thumb-news {
        padding-bottom: 60%;
    }

    &.thumb-1x1 {
        padding-bottom: 100%;
    }

    &.thumb-1x2 {
        padding-bottom: 50%;
    }

    &.thum-2x3 {
        padding-bottom: 150%;
    }

    img {
        -o-object-fit: cover;
        object-fit: cover;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
    }
}

.section {
    padding: 80px 0;

    @media (max-width:991px) {
        padding: 30px 0;
    }
}

.placeholder {

    position: relative;
    background: #222222 !important;
    opacity: 0.1;
    animation: fading 1.5s infinite;
    border-radius: 4px;
}

@keyframes fading {
    0% {
        opacity: 0.1;
    }

    0% {
        opacity: 0.2;
    }

    100% {
        opacity: 0.1;
    }
}


.checkbox {
    width: 18px;
    height: 18px;
}

.breadcrumb {
    width: 100%;
    padding: 16px 0;
    background: linear-gradient(270deg, #FFF 0%, #FEF7EE 100%);

    a {
        color: #888
    }

    li {
        display: inline-block;

        font-size: 16px;

        &::after {
            margin: 0 10px;
            content: '\2022';
            color: #888;
