}

.box-checkbox {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-right: 5px;

    input {
        padding: 0;
        height: initial;
        width: initial;
        margin-bottom: 0;
        display: none;
        cursor: pointer;
    }

    label {
        position: relative;
        cursor: pointer;
        line-height: 22px;

        &:hover {
            &::before {
                border: 1px solid #FD5023;
            }
        }
    }

    label:before {
        content: '';
        -webkit-appearance: none;
        background-color: transparent;
        border: 1px solid #C2C2C2;
        border-radius: 4px;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
        padding: 10px;
        display: inline-block;
        height: 22px;
        width: 22px;
        position: relative;
        vertical-align: middle;
        cursor: pointer;
    }

    input:checked {
        &+label::before {
            border: 1px solid var(--color-base1);
            background: #fff;

        }

        &+label:after {
            content: '';
            display: block;
            position: absolute;
            top: 4px;
            left: 8px;
            width: 6px;
            height: 13px;
            border: solid var(--color-base1);
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
        }
    }
}

.err {
    display: block;
    font-size: 12px;
    margin-top: 3px;
    color: #FD5023;
}

