.box-radio {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-right: 5px;

    input {
        padding: 0;
        height: initial;
        width: initial;
        margin-bottom: 0;
        display: none;
        cursor: pointer;
    }

    label {
        position: relative;
        cursor: pointer;
        line-height: 12px;

        &:hover {
            &::before {
                border: 1px solid #FD5023;
            }
        }
    }

    label:before {
        content: '';
        -webkit-appearance: none;
        background-color: transparent;
        border: 1px solid #C2C2C2;
        border-radius: 50%;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
        padding: 10px;
        display: inline-block;
        height: 12px;
        width: 12px;
        position: relative;
        vertical-align: middle;
        cursor: pointer;
    }

    input:checked {
        &+label::before {
            border: 1px solid #FD5023;
            background: #FD5023;

        }

        &+label:after {
            content: '';
            display: block;
            position: absolute;
            top: 3px;
            left: 8px;
            width: 6px;
            height: 13px;
            border: solid #fff;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
        }
    }
}

.list-news {
    @media (max-width:991px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
    }

    @media (max-width:767px) {
        grid-template-columns: repeat(1, 1fr);
        gap: 0;
    }

    .item-news {
        margin-bottom: 25px;
        // background: #FCFCFC;
        // box-shadow: 0px 4px 30px 0px #3629B712;
        // border-radius: 16px;
        // box-shadow: 4px 0px 8px 0px #3629B714;

        @media (max-width:991px) {
            margin-bottom: 0;
        }

        &:last-of-type {
            margin-bottom: 0;
        }

        .box-news {
            display: flex;
            align-items: start;

            @media (max-width:991px) {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
            }

            @media (max-width:991px) {
                display: grid;
                grid-template-columns: repeat(1, 1fr);
                gap: 0;
            }
        }

        .thumb-art {
            width: calc(100%/3);
            border-radius: 8px;
            overflow: hidden;
            min-width: calc(100%/3);

            @media (max-width:991px) {
                width: 100%;
            }
        }

        .title-news {
            font-size: 16px;
            font-weight: 700;
            color: var(--color-base);
            text-transform: uppercase;

            &:hover {
                color: var(--color-base);

                a {
                    color: var(--color-base);
                }
            }
        }

        .description {
            font-size: 16px;
            color: #616161;
            margin-top: 10px;
            // display: -webkit-box;
            // -webkit-line-clamp: 3;
            // word-break: break-word;
            // overflow: hidden;
            // -webkit-box-orient: vertical;

            @media (max-width:1680px) {
                -webkit-line-clamp: 2;
            }

            @media (max-width:991px) {
                -webkit-line-clamp: inherit;
            }

        }

        .right-content {
            padding-left: 20px;

            @media (max-width:991px) {
                padding-left: 0;
                padding-top: 15px;
            }
        }


        .flex-cat {
            display: flex;
            align-items: center;
            gap: 5px;
            row-gap: 10px;
            margin-bottom: 10px;

            @media (max-width:991px) {
                flex-wrap: wrap;
            }

            .time {
                display: flex;
                align-items: center;
                gap: 5px;
                color: #919191;
                font-size: 13px;
                font-weight: 400;

                .icon {
                    width: 16px;
                    height: 16px;
                }
            }

            .cat-tag {
                font-size: 13px;
                padding: 0 5px;
                color: #919191;
                text-transform: uppercase;
                border-left: 1px solid #919191;
            }
        }

        .xemthem {
            color: #1349D4;
            font-size: 13px;
            margin-top: 10px;
            display: block;

            @media (max-width:767px) {
                margin-top: 0;
            }

            &::after {
