.grid-2 {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));

    @media (max-width:767px) {
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }
}

.section-news-detail {
    .content {
        .thumb-art {
            .thumb-news {
                border-radius: 16px;
                padding-bottom: 35%;
            }

            margin-bottom: 30px;
        }

        .flex-cat {
            display: flex;
            align-items: center;
            gap: 5px;
            row-gap: 10px;
            margin-bottom: 10px;

            @media (max-width:991px) {
                flex-wrap: wrap;
            }

            .time {
                display: flex;
                align-items: center;
                gap: 5px;
                color: #51555F;
                font-size: 13px;
                font-weight: 400;

                .icon {
                    width: 16px;
                    height: 16px;
                }
            }

            .cat-tag {
                font-size: 13px;
                padding: 0 5px;
                color: #51555F;
                border-left: 1px solid #51555F;
            }
        }

        .title-news {
            font-size: 36px;
            font-weight: 800;
            line-height: 120%;
            margin-bottom: 15px;
            text-transform: uppercase;
            color: var(--color-base);

            @media (max-width:991px) {
                font-size: 24px;
            }
        }

        .flex-cat {
            margin-bottom: 25px;
            text-transform: uppercase;

            .cat-tag {
                margin-right: 10px;
            }
        }

        .details {
            font-size: 16px;
            color: #5A5D72;

            p {
                margin-bottom: 15px;
            }

            img {
                margin: auto;
                margin-bottom: 15px;
                max-width: 100%;
                max-height: 100%;
                object-fit: contain;
            }

            h2,
            h3,
            h4,
            h5,
            h6 {
                margin-bottom: 15px;
            }
        }

    }

}

.section-product-detail {
    padding: 60px 0;
    font-size: 16px;
    background: var(--Linear, linear-gradient(180deg, #FEF7EE 0%, #FFF 100%));

    @media (max-width:767px) {
        padding: 30px 0;
    }

    .short-lead {
        color: var(--color-base1);
        font-size: 16px;
        margin-bottom: 15px;
    }

    .title-news {
        font-size: 36px;
        font-weight: 800;
        line-height: 120%;
        margin-bottom: 15px;
        text-transform: uppercase;
        color: var(--color-base);

        @media (max-width:991px) {
            font-size: 24px;
        }

    }


    .details {
        font-size: 16px;
        color: #5A5D72;

        p {
            margin-bottom: 15px;
        }

        img {
            margin: auto;
            margin-bottom: 15px;
            max-width: 100%;
            max-height: 100%;
            object-fit: contain;
        }

        h2,
        h3,
        h4,
        h5,
        h6 {
            margin-bottom: 15px;
        }
    }

    .siderbar-news {
        @media (max-width:768px) {
            order: -1;
            margin-bottom: 30px;
        }

        .item-product {
            padding: 0;
            overflow: hidden;

            .thumb {
                border-radius: 12px 12px 0 0;

            }

            &:hover {
                transform: none;
            }
        }

        .box-info {
            padding: 16px;
            background: #fff;
            border-radius: 0 0 16px 16px;

            p {
                color: #6D6D6D;
            }

            ul {
                position: static;
                margin-top: 8px;
            }

            .block-price {
                display: flex;
                gap: 5px;
                align-items: center;

                span {
                    color: #E9722C;
                    font-size: 24px;
                    font-weight: 900;
                    line-height: 150%;
                    /* 36px */
                }

                strike {
                    color: #6D6D6D;
                    font-size: 16px;
                    font-weight: bold;
                }
            }

            .btn {
                text-transform: uppercase;
                width: 100%;
                padding: 8px;
                font-weight: bold;
                margin-top: 8px;
                display: block;

                &.btn-line {
                    background: #F0F7FD;
                }
            }
        }
    }

    .box-des-nobg {
        color: #11233A;
        margin-top: 24px;
    }

    .box-des {
        border-radius: 16px;
        box-shadow: 0px 4px 35px 0px rgba(126, 126, 126, 0.10);
        background: #fff;
        padding: 24px;
        color: #11233A;
        margin-top: 24px;
        position: relative;

        .breadcrumb {
            background: none;
            padding: 0;
        }

        @media (max-width:991px) {
            padding: 15px;
        }

        h4 {
            color: var(--color-base1);
            font-size: 18px;
            font-weight: 700;
            line-height: 150%;
            /* 27px */
            text-transform: uppercase;
            margin-bottom: 15px;
        }

        .list {
            display: grid;
            grid-template-columns: repeat(2, minmax(0, 1fr));
            column-gap: 24px;
            row-gap: 15px;

            @media (max-width: 767px) {
                grid-template-columns: repeat(1, minmax(0, 1fr));
            }

            li {
                position: relative;
                display: flex;
                gap: 10px;

                &::before {
                    flex-shrink: 0;
                    width: 24px;
                    content: url('data:image/svg+xml,<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M21.5306 7.28062L9.53061 19.2806C9.46096 19.3503 9.37824 19.4057 9.2872 19.4434C9.19615 19.4812 9.09855 19.5006 8.99999 19.5006C8.90143 19.5006 8.80383 19.4812 8.71278 19.4434C8.62174 19.4057 8.53902 19.3503 8.46936 19.2806L3.21936 14.0306C3.07863 13.8899 2.99957 13.699 2.99957 13.5C2.99957 13.301 3.07863 13.1101 3.21936 12.9694C3.3601 12.8286 3.55097 12.7496 3.74999 12.7496C3.94901 12.7496 4.13988 12.8286 4.28061 12.9694L8.99999 17.6897L20.4694 6.21936C20.6101 6.07863 20.801 5.99957 21 5.99957C21.199 5.99957 21.3899 6.07863 21.5306 6.21936C21.6713 6.36009 21.7504 6.55097 21.7504 6.74999C21.7504 6.94901 21.6713 7.13988 21.5306 7.28062Z" fill="%23E9722C"/></svg>')
                }
            }
        }

        .xemthem {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 8px;
            color: #1F7CCC;
            margin-top: 12px;

            &::before {
                content: '';
                border-radius: 10px;
                display: block;
                height: 150px;
                width: 100%;
                position: absolute;
                bottom: 0;
                left: 0;
                background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%);
            }

            span {
                position: relative;
                font-size: 14px;
                font-weight: 400;
                line-height: 150%;
            }

            &::after {
                position: relative;
                line-height: 16px;
                content: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="17" viewBox="0 0 16 17" fill="none"><path d="M13.5306 7.03025L8.53063 12.0302C8.46095 12.1002 8.37816 12.1556 8.28699 12.1935C8.19583 12.2314 8.09809 12.2508 7.99938 12.2508C7.90067 12.2508 7.80293 12.2314 7.71176 12.1935C7.6206 12.1556 7.53781 12.1002 7.46813 12.0302L2.46813 7.03025C2.32723 6.88935 2.24808 6.69825 2.24808 6.499C2.24808 6.29974 2.32723 6.10864 2.46813 5.96775C2.60902 5.82685 2.80012 5.7477 2.99938 5.7477C3.19864 5.7477 3.38973 5.82685 3.53063 5.96775L8 10.4371L12.4694 5.96712C12.6103 5.82623 12.8014 5.74707 13.0006 5.74707C13.1999 5.74707 13.391 5.82623 13.5319 5.96712C13.6728 6.10802 13.7519 6.29911 13.7519 6.49837C13.7519 6.69763 13.6728 6.88873 13.5319 7.02962L13.5306 7.03025Z" fill="%231F7CCC"/></svg>');
            }
        }

        p {
            &:not(:last-child) {
                margin-bottom: 12px;
            }
        }
    }

    .title-box {
        color: var(--color-base1);
        font-size: 18px;
        font-weight: 700;
        line-height: 150%;
        /* 27px */
        text-transform: uppercase;
        margin-bottom: 15px;
    }

    .block-course {
        background: #F2F8FD;

        .item {
            .title {
                display: flex;
                width: 100%;
                justify-content: space-between;
                padding: 16px 10px;
                border-bottom: 1px solid #8AC2EF;
                background: #E3EEFB;
                color: #11233A;
                font-size: 18px;
                font-weight: 700;
                line-height: 150%;
                /* 27px */
