.col-flex {
    display: flex;
    gap: 30px;

    .col-left {
        width: calc(100% - 330px);
    }

    .col-right {
        width: 300px;
        flex-shrink: 0;
    }

    @media (max-width:767px) {
        flex-wrap: wrap;

        .col-left {
            width: 100%;
        }

        .col-right {
            display: none;
        }
    }
}

